import "./styles.css";
import menu from "./menu.json";
import React from "react";

import Card from "./card.js";
import Image from "./image.js";
import Appbar from "./appbar.js";

function menus(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);
  for (let i = 0; i < values.length; i++) {
    if (keys[i] === "写真") {
      items.push(<img src={values[i]} alt="画像の説明"></img>);
    } else {
      items.push(
        <li>
          {keys[i]}:{values[i]}
        </li>
      );
    }
  }

  return <ul>{items}</ul>;
}
function all_menus() {
  const items = [];
  for (let i = 0; i < menu.length; i++) {
    items.push(menus(i));
  }
  return <>{items}</>;
}

export default function App() {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <>
      <Appbar />
      <h1 style={{ fontFamily: "serif" }}>GALARIA CHOCOLATE</h1>

      <Card />
      <div style={{ display: "flex" }}>{all_menus()}</div>
      <br />
      <br />
      <br />
      <Image />
    </>
  );
}
